import Vue from "vue";
import Router from "vue-router";
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/index",
      name: "index",
      component: () => import("@/page/index/index")
    },
    {
      path: "/personal",
      name: "personal",
      component: () => import("@/page/personal")
    },
    {
      path: "/rank",
      name: "rank",
      component: () => import("@/page/rank")
    },
    {
      path: "/lottery",
      name: "lottery",
      component: () => import("@/page/lottery")
    },
    {
      path: "/collect",
      name: "collect",
      component: () => import("@/page/lottery/collect.vue")
    },
    {
      path: "/join",
      name: "join",
      component: () => import("@/page/join")
    },
    {
      path: "/",
      name: "demo",
      component: () => import("@/page/demo")
    },
    {
      path: "/edit",
      name: "edit",
      component: () => import("@/page/edit")
    }
  ]
});



