import Vue from "vue";
import axios from "axios";
import router from "./router";
import wx from "weixin-js-sdk";
import qs from "qs";
import VueDraggableResizable from "vue-draggable-resizable";
import "vue-draggable-resizable/src/components/vue-draggable-resizable.css";
Vue.component("drag_resize", VueDraggableResizable);
import vue_cookie from "vue-cookie";
import "../static/reset.css";
import "../static/global.css";
import "../config/rem";

const api_config = require("../api_config.js");
Vue.config.productionTip = false;

axios.defaults.baseURL = "http://ysl.entfly.com";
axios.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded; charset=utf-8";
axios.interceptors.request.use(
  config => {
    if (config.method === "post") {
      // console.log(qs.stringify(config.data));
      config.data = qs.stringify(config.data);
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);
axios.interceptors.response.use(
  res => {
    return res;
  },
  err => {
    return Promise.reject(err + "1");
  }
);
Vue.prototype.$http = axios;
Vue.prototype.$cookie = vue_cookie;
//this.$cookie.set('name','1',1) 存储cookie 键，值，时间(1天)
//this.$cookie.get('name') 获取cookie 键
//this.$cookie.delete('name') 删除cookie 键
Vue.prototype.$api = api_config;

// 微信sdk
wx.baseUrl = "https://api.weixin.qq.com"; //域名

wx.error(function(res) {
  // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
});
Vue.prototype.$wx = wx;
new Vue({
  el: "#app",
  router
});



